.div-ex {
  padding-left: 1.5rem/* 24px */;
  padding-right: 1.5rem/* 24px */;
  align-items: flex-start;
  font-family: Montserrat;
  
  --tw-bg-opacity: 1;
  background-color: rgb(201 228 235 / var(--tw-bg-opacity));


  @media (min-width: 768px) {
        width: 100%;

        height: auto;
    
  }
}

.navbar{
  display: flex;
  flex-direction: row;
  align-items: center;
  background-color: #00494D;
  color: #ffcd99;
  font-family: sans-serif;
  width: 100%;
  height: 70px;
  overflow: hidden;
  transition: 0.3s ease-in-out;

}

.sticky {
  position: sticky;
  top: 0;
  z-index: 100;
}

.nav-div {
  align-items: center;
  justify-content: space-between;
  margin-left: 10rem;
  margin-right: 5rem;
  width: 100%;  

  @media (max-width: 768px) {
    margin-left: 1rem;
    margin-right: 0rem;

  }
}

.align-center {
  align-items: center;
  
}

.nav-list {
  display: flex;
  flex-direction: row;
  place-content: space-between;
  width: 70%;
  align-items: center;

  @media (max-width: 768px) {
    flex-direction: column;
    width: 100%;
    position: absolute;
    top: 60px;
    left: 0;
    height: calc(100vh - 77px);
    place-content:initial;
  }
}

.nav-div button {
  border: 0;
  height: 40px;
  width: 40px;
  padding: 0.5rem;
  border-radius: 50%;
  cursor: pointer;
  transition: background-color 0.2s ease-in-out;
  position: absolute;
  right: 25px;
  transform: translateY(-50%);
  display: none;

  @media screen and (max-width: 768px) {
      display: block;
  }
}
.navigation-menu {
  margin-left: auto;  
}

.navigation-menu ul {
  padding: 0;
  
  @media screen and (max-width: 768px) {
    display: none;
    position: absolute;
    top: 60px;
    left: 0;
    flex-direction: column;
    width: 100%;

    height: 40vh;
    background-color: #00494D;
    border-top: 1px solid #BDFFEF;
    border-bottom: 1px solid #BDFFEF;
  }
}

.navigation-menu.expanded ul {
  display: flex;
  @media screen and (max-width: 768px) {
    display: block;
    position: fixed;
  }
}
.navigation-menu li {
  list-style-type: none;
  margin: 0 1rem;
}

.navigation-menu li a {
  text-decoration: none;
  display: block;
  width: 100%;

  @media screen and (max-width: 767px) {
    color: #FFFFED;
    width: 100%;
    padding: 0.5rem 0;
  }
}

.nav-list li {
  display: block;
  align-self: center;
  color: var(--white, #FFFFED);
  font-family: Fira Code;
  font-size: 16px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  position: relative;
  padding-bottom: 4px; /* Adjust this value to control the height of the underscore */
  text-decoration: none;
  margin-right: 35px;

  @media screen and (max-width: 768px) {
    text-align: center;
    padding: 5px;
  }
}

.nav-list li em {
  color: var(--primary, #FF8A44);
  font-family: Fira Code;
  font-size: 16px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
}

.nav-list li::after {
  @media  screen and (min-width: 768px) { 
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    width: 0;
    border-radius: 25px;
    height: 4px; /* Adjust this value to control the thickness of the underscore */
    background-color: #FF6A3D; /* Adjust this value to change the color of the underscore */
    transition: width 0.3s ease-in-out; /* Adjust the animation speed as needed */
  }
}

/* Add hover effect to the list items */
.nav-list li:hover::after {
  width: 100%;
}

